import { LOG_IN, LOG_OUT, LOG_IN_FAILURE } from './auth-constants';

const initialState = {
  user: null,
  errorMsg: '',
  isAuth: false
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case LOG_IN:
      return {
        ...state,
        user: {
          name: action.payload.name
        },
        errorMsg: '',
        isAuth: true
      };
    case LOG_OUT:
      return {
        ...state,
        user: null,
        errorMsg: '',
        isAuth: false
      };
    case LOG_IN_FAILURE:
      return {
        ...state,
        errorMsg: action.payload.errorMsg
      };
    default:
      return state;
  }
};

export default authReducer;
